<template>
    <div :class="`scroll-square ${big?'square-big':''}`">
        <router-link :to="`/${type}?id=${album.id}`">
            <div class="preview-image" :style="`background-image: url(${image})`"></div>
        </router-link>
        <p class="preview-title">{{album.name}}</p>
        <p v-if="album.hasOwnProperty('description')" class="preview-description md-accent"
           v-html="album.description"/>
    </div>
</template>

<script>
    export default {
        name: "AlbumSquare",
        props: {
            big: {
                type: Boolean,
                default: false,
            },
            album: Object,
            type: {
                type: String,
                default: 'album',
            },
        },
        data() {
            return {
                image: '',
            }
        },
        mounted() {
            if (this.album.images.length > 0)
                this.image = this.album.images[0].url;
            else
                this.image = 'img/notfound.png';
        },
        methods: {
            openPage(id) {
                this.$router.push('/' + this.type + '?id=' + id);
            }
        }
    }
</script>

<style scoped>

    .scroll-square {
        min-width: 120px;
        min-height: 140px;
        margin-right: 10px;
        cursor: pointer;
        max-width: 120px;
    }

    .scroll-square:last-child {
        padding-right: 135px;
    }

    .square-big {
        min-width: 160px;
        max-width: 160px;
        min-height: 180px;
    }

    .square-big:last-child {
        padding-right: 175px;
    }

    .square-big .preview-image {
        min-width: 160px;
        min-height: 160px;
    }

    .preview-image {
        min-width: 120px;
        min-height: 120px;
        background-size: cover;
        background-position: center;
        background-repeat: no-repeat;
        border-radius: 3px;
        box-shadow: 0 0 20px 0 rgba(0, 0, 0, 0.3);
    }

    .preview-title {
        font-size: 13px;
        margin: 5px;
        min-width: 120px;
        font-weight: bold;
        line-height: 150%;
        max-height: 60px;
        overflow-y: hidden;
    }

    .preview-description {
        font-size: 11px;
        margin: 5px;
        min-width: 120px;
        text-overflow: ellipsis;
        word-wrap: break-word;
        overflow: hidden;
        max-height: 60px;
        opacity: 0.7;
        font-weight: bolder;
        line-height: 150%;
    }
</style>